#MessageModal{
    color:white;
    width:80%;
    text-align:center;
    position:fixed;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    font-weight:600;
    display:block;
    opacity: 0;
    transition: all 0.5s;
}
  
#MessageModal.set{
    background-color:#168b12;
    display:block;
    opacity: 1;
    top:calc(50% + 20px);
}
  
#MessageModal.not-set{
    background-color:#990000;
    display:block;
    opacity: 1;
    top:calc(50% + 20px);
}
  
#MessageModal.hide{
    display:none;
}
  
@media only screen and (min-width: 600px) {
    #MessageModal{
      width:60%;
      max-width:400px;
    }
} 